.Spinner_container__CkSGv {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
}

.Spinner_spinner__1geWs {
  position: relative;
  border: 4px solid var(--green);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border-left-color: rgba(0 , 0 , 0, .1);
  -webkit-animation: Spinner_spin__2subW .9s linear infinite;
          animation: Spinner_spin__2subW .9s linear infinite;
}

@-webkit-keyframes Spinner_spin__2subW {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Spinner_spin__2subW {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



